import { createApp } from 'vue' // vue3
import App from './App.vue'

import io from 'socket.io-client';
const SERVER_IP = 'http://172.16.1.141'//'localhost';
const SERVER_PORT = '3000';
//const socket = io('http://172.16.1.141:3000'); // Server IP:PORT
const socket = io(SERVER_IP+':'+SERVER_PORT);
//Vue.prototype.$socket = socket; //vue3 X


//createApp(App).mount('#app')
const app = createApp(App);
app.config.globalProperties.$socket = socket;
/*
app은 루트 컴포넌트 인스턴스
app.config.globalProperties.$socket = socket; 이 부분에서 socket 인스턴스를 $socket 변수에 할당
*/

app.mount('#app')

/*
Vue3에서 전역변수 사용
createApp(루트 컴포넌트).config.globalProperties.변수명 = 할당할 값 으로 선언해라.
getCurrentInstance().appContext.config.globalProperties.변수명으로 받아와라.
getCurrentInstance() 메소드는 setup() 혹은 LifeCycle Hook에서만 쓸 수 있다.
*/
