<template>
    <div><!--audio-->
        <!--
        <div v-if="status_all.exec_mon1 == 1">{{ audioFunctions.audio_Alarm_play(app_Info.isMute) }}</div>
        <div v-else>{{ audioFunctions.audio_Alarm_stop() }}</div>
        -->      
    </div>

    <div class="box-tab"> <!-- @click="audioFunctions.push_alarm()">--> 
    <div class="box-content">
        <div class="box-container">
            <div class="box1">
                <div class="box1-1"></div>
                <div class="box1-2">
                    <div class="box1-2-1">{{ airport_info.name }}</div>
                    <div class="box1-2-2"><current_time/></div>
                </div>
            </div>

            <div class="box2">
                <table cellspacing="10">
                    <tr>
                        <td><img class="cat" src="@/../public/category/CAT-3_OFF.png" alt="cat-3"></td>
                        <td><img class="cat" src="@/../public/category/CAT-2_OFF.png" alt="cat-2"></td>
                        <td><img class="cat" src="@/../public/category/CAT-1.png" alt="cat-1"></td>
                        <td><img class="cat" src="@/../public/category/CAT-LOC_OFF.png" alt="cat-LocOnly"></td>
                    </tr>
                </table>
            </div>

            <div class="box3">
                <div class="box3-1">
                    <div class="box3-1-1"></div>
                    <div class="box3-1-2"><!----></div>
                    <div class="box3-1-3"></div>
                </div>
                <div class="box3-2">
                    <div class="box3-2-1" v-on:click="app_Info.isMute = !app_Info.isMute">  
                        <!--
                        <table class="silence_table" v-if="(app_Info.isPlaying == true) && app_Info.isMute == false">
                            <tr><img class="alarm_icon" src="@/../public/etc/sound_icon.png" alt="silence" height="40" width="40"></tr>  class = alarm_icon 
                            <tr><em>silence</em></tr>
                        </table>
                        -->
                        <table class="silence_table" v-if="(app_Info.isPlaying == true) && app_Info.isMute == false">
                            <tr><img class="sound_icon" src="@/../public/etc/sound_icon.png" alt="silence"></tr> 
                            <tr><em>silence</em></tr>
                        </table>
                    </div>
                    <div class="box3-2-2">
                        <img class="move_r" src="@/../public/airplane/aircraft_right.png" alt="move_Airplane">
                    </div>
                    <div class="box3-2-3">
                        <div class="box3-2-3-1"><!----></div>
                        <div class="box3-2-3-2"><!----></div>
                    </div>
                </div>
                <div class="box3-3">
                    <div class="box3-3-1"></div>
                    <div class="box3-3-2">
                        <table class="equip_table">
                            <tr>
                                <td>
                                    <div class="exec-equip">
                                        <img class="exec-equip" alt="loc"
                                            :src='statusFunctions.imagePath_exec(status_loc_1.equip, status_loc_1.exec_bypass, status_loc_1.acvie, status_loc_1.tx1_onoff, status_loc_1.tx2_onoff, status_loc_1.exec_mon1, status_loc_1.exec_mon2, true)'>
                                        <div class="stby-equip">
                                            <img class="stby-equip" alt="loc-stby"
                                                :src='statusFunctions.imagePath_stby(status_loc_1.exec_bypass, status_loc_1.acvie, status_loc_1.tx1_onoff, status_loc_1.tx2_onoff, status_loc_1.nfm_mon1, status_loc_1.nfm_mon2, true, true)'>
                                        </div>
                                    </div>
                                </td>
                                <td class="num_display_right">{{ runway.left_num }}</td>
                            </tr>
                            <tr>
                                <td>
                                    <div class="exec-equip">
                                        <img class="exec-equip" alt="dme-exec"
                                            :src='statusFunctions.imagePath_exec(status_dme_1.equip, status_dme_1.exec_bypass, status_dme_1.acvie, status_dme_1.tx1_onoff, status_dme_1.tx2_onoff, status_dme_1.exec_mon1, status_dme_1.exec_mon2, true)'>
                                        <div class="stby-equip">
                                            <img class="stby-equip" alt="dme-stby"
                                                :src='statusFunctions.imagePath_stby(status_dme_1.exec_bypass, status_dme_1.acvie, status_dme_1.tx1_onoff, status_dme_1.tx2_onoff, status_dme_1.nfm_mon1, status_dme_1.nfm_mon2, false, true)'>
                                        </div>
                                    </div>
                                </td>
                                <td class="num_display_right">{{ runway.left_num }}</td>
                            </tr>
                        </table>
                    </div>
                    <div class="box3-3-3"></div>
                </div>
            </div>

            <div class="box4">
                <div class="box4-1">{{ runway.left_num }}</div>
                <div class="box4-2" :style="{background: statusFunctions.color_main(status_all.maintenance, status_all.status)}">{{ statusFunctions.main_Status(status_all.maintenance, status_all.status) }}</div>
                <div class="box4-3">{{ runway.right_num }}</div>
            </div>
        </div>
    </div>
</div>

</template>
  
<script>
import { getCurrentInstance, onMounted, onUnmounted, reactive, computed, ref } from "@vue/runtime-core";
// @ is an alias to /src
import current_time from '@/components/Time.vue' // 사용
import statusFunctions from './status_bind.js'
import audioFunctions from './audio_play.js'
import Menu from '@/components/Menu.vue' 
import io from 'socket.io-client'
export default {
    name: 'Pohang',
    props: { // 부모로 부터 데이터를 받을 수 있음. 허용할 props선언
    },
    components: {
        Menu,
        current_time, // 사용
    },
    setup() {
        //main.js에서 $socket에 저장했던 socket변수는 getCurrentInstance().appContext.config.globalProperties. $socket에 저장되어있다.
        const app = getCurrentInstance();
        const $socket = app.appContext.config.globalProperties.$socket;
        //const $socket = io('http://172.16.1.141:3000', {  autoConnect: true});

        const airport_info = {
            name : "Pohang Airport",
        };
        const runway = {
            left_num : "10",
            right_num : "28",     
        };
        const app_Info = reactive({
            isConnect : false,
            userInfo : '',
            isMute : false,
            isPlaying : false,
        });
        const audio_param = {
            is10LOCPushOK : false,
            is10DMEPushOK : false,
            audiolists : new Array(),
            pointer : 0,
            audiolists_len : 0,
            audiolists_pop_len : 0,
        }
/*
        const playTimer_Period = 5000;
        const pushTimer_Period = playTimer_Period - 2000;
        var playTimer;
        var pushTimer;
        var isPlaying = false;
        var audiolists = new Array();
        var pointer = 0;
        var audiolists_len = 0;
        var audiolists_pop_len = 0;
        var isLOCPushOK = false;
        var isDMEPushOK = false;
        var isTestPushOK = false;
*/
        const EQUIP_LOC1_IP = '172.16.1.53' // loc
        const EQUIP_DME1_IP = '172.16.1.54' // dme

        const status = ({
            equip : "",
            maintenance : false,
            acvie : "tx1",
            tx1_onoff : false,
            tx2_onoff : false,
            exec_bypass : false,
            exec_mon1 : 3,
            exec_mon2 : 3,
            exec_mon : 3,
            nfm_bypass : false,
            nfm_mon1 : 3,
            nfm_mon2 : 3,
            nfm_mon : 3,
            stby_bypass : false,
            stby_mon : 3,
            status : 3,
        });
        //const status_all = reactive({status});
        const status_all = reactive({
            equip : "",
            maintenance : false,
            acvie : "tx1",
            tx1_onoff : true,
            tx2_onoff : true,
            exec_bypass : false,
            exec_mon1 : 3,
            exec_mon2 : 3,
            nfm_bypass : false,
            nfm_mon1 : 3,
            nfm_mon2 : 3,
            stby_bypass : false,
            stby_mon : 3,
            status : 3,
        });
        //const status_loc_1 = reactive({status});

        const status_loc_1 = reactive({
            equip : "",
            maintenance : false,
            acvie : "tx1",
            tx1_onoff : true,
            tx2_onoff : true,
            exec_bypass : false,
            exec_mon1 : 3,
            exec_mon2 : 3,
            nfm_bypass : false,
            nfm_mon1 : 3,
            nfm_mon2 : 3,
            stby_bypass : false,
            stby_mon : 3,
            status : 3,
        });
        
        const status_loc_2 = reactive({status});
        const status_gp_1 = reactive({status});
        const status_gp_2 = reactive({status});

        //const status_dme_1 = reactive({status});
        const status_dme_1 = reactive({
            equip : "",
            maintenance : false,
            acvie : "tx1",
            tx1_onoff : true,
            tx2_onoff : true,
            exec_bypass : false,
            exec_mon1 : 3,
            exec_mon2 : 3,
            nfm_bypass : false,
            nfm_mon1 : 3,
            nfm_mon2 : 3,
            stby_bypass : false,
            stby_mon : 3,
            status : 3,
        });

        const status_dme_2 = reactive({status});

        const playTimer_Period = 5000; // 7000
        const pushTimer_Period = playTimer_Period - 2000;

        //var pushTimer = setInterval(alarmAudioList_Handler, pushTimer_Period); console.log("[ 0-1 ] Audio Push Timer Start !! ");
        //var playTimer = setInterval(audioAlarmPlay_Handler, playTimer_Period); console.log("[ 0-2 ] Audio Play Timer Start !! ");
        var pushTimer;
        var playTimer;

        onMounted(() => {
            console.log(`컴포넌트가 마운트 됐습니다.`);
            //localStorage.setItem // 브라우저에 값을 저장해주는 저장소 // 추후 찾아보기

            $socket.on("connect", () => {
                app_Info.isConnect = $socket.connected; // true
                console.log("Server connect");
                pushTimer = setInterval(alarmAudioList_Handler, pushTimer_Period); console.log("[ 0-1 ] Audio Push Timer Start !! ");
                playTimer = setInterval(audioAlarmPlay_Handler, playTimer_Period); console.log("[ 0-2 ] Audio Play Timer Start !! ");
            });

            $socket.on("disconnect", () => {
                app_Info.isConnect = $socket.connected; // false
                console.log("Server disconnect"); // false
                clearInterval(playTimer);
                clearInterval(pushTimer);
            });

            $socket.io.on("reconnect", () => {
                // ...
                console.log("reconnectreconnectreconnectreconnect"); //  $socket.on("connect", () => { 와 거의 같은 듯
            });

            $socket.on('SEND', (data) => {
                let server_msg;
                server_msg = JSON.parse(data); // JSON파싱
                console.log(`서버 소켓에게 받은 데이터: `, server_msg);

                if (server_msg.ip == EQUIP_LOC1_IP) {
                    status_loc_1.equip = server_msg.conf.equip;
                    status_loc_1.maintenance = server_msg.maint;
                    status_loc_1.acvie = server_msg.sys.active;
                    status_loc_1.tx1_onoff = server_msg.tx1.on;
                    status_loc_1.tx2_onoff = server_msg.tx2.on;
                    status_loc_1.exec_bypass = server_msg.exec.bypass;
                    status_loc_1.exec_mon1 = server_msg.exec.mon1;
                    status_loc_1.exec_mon2 = server_msg.exec.mon2;
                    //status_loc_1.exec_mon = 0;
                    status_loc_1.nfm_bypass = server_msg.nfm.bypass;
                    status_loc_1.nfm_mon1 = server_msg.nfm.mon1;
                    status_loc_1.nfm_mon2 = server_msg.nfm.mon2;
                    //status_loc_1.nfm_mon = 0;
                    status_loc_1.stby_bypass = server_msg.stb.bypass;
                    status_loc_1.stby_mon = server_msg.stb.mon;
                }
                if (server_msg.ip == EQUIP_DME1_IP) {
                    status_dme_1.equip = server_msg.conf.equip;
                    status_dme_1.maintenance = server_msg.maint;
                    status_dme_1.acvie = server_msg.sys.active;
                    status_dme_1.tx1_onoff = server_msg.tx1.on;
                    status_dme_1.tx2_onoff = server_msg.tx2.on;
                    status_dme_1.exec_bypass = server_msg.exec.bypass;
                    status_dme_1.exec_mon1 = server_msg.exec.mon1;
                    status_dme_1.exec_mon2 = server_msg.exec.mon2;
                    status_dme_1.nfm_bypass = server_msg.nfm.bypass;
                    status_dme_1.nfm_mon1 = server_msg.nfm.mon1;
                    status_dme_1.nfm_mon2 = server_msg.nfm.mon2;
                    //status_dme_1.nfm_mon = 0;
                    status_dme_1.stby_bypass = server_msg.stb.bypass;
                    status_dme_1.stby_mon = server_msg.stb.mon;
                }
                //status_all.maintenance = status_loc_1.maintenance | status_dme_1.maintenance;
                status_all.maintenance = status_loc_1.exec_bypass | status_dme_1.exec_bypass; // 추후 수정(maint)

                //status_loc_1.exec_mon = statusFunctions.mon_Status(status_loc_1.exec_mon1, status_loc_1.exec_mon2);
                //status_dme_1.exec_mon = statusFunctions.mon_Status(status_dme_1.exec_mon1, status_dme_1.exec_mon2);
                status_loc_1.status = statusFunctions.selected_equip_status(status_loc_1.acvie, status_loc_1.tx1_onoff, status_loc_1.tx2_onoff, status_loc_1.exec_mon1, status_loc_1.exec_mon2, true);
                status_dme_1.status = statusFunctions.selected_equip_status(status_dme_1.acvie, status_dme_1.tx1_onoff, status_dme_1.tx2_onoff, status_dme_1.exec_mon1, status_dme_1.exec_mon2, true);
                
                status_all.status = (status_loc_1.status >= status_dme_1.status) ? status_loc_1.status : status_dme_1.status;
            });
        });

        function getAlarmSound(pointer) {
            var returnVal = audio_param.audiolists[pointer];
            return returnVal;
        }

        const audio_alarm = new Audio();
        function audioAlarmPlay_Handler() {
            if(audio_param.audiolists.length != 0) {
                clearInterval(playTimer); console.log("[ 2 ] Stop Play Timer!! Push Stop!! First Audio Playing");
                app_Info.isPlaying = true;
                audio_alarm.muted = app_Info.isMute; console.log("[ 2 - First ] 음소거 체크 !! app_Info.isMute : ", app_Info.isMute); 
                audio_alarm.src = getAlarmSound(audio_param.pointer);
                var playPromise = audio_alarm.play();
                if (playPromise !== undefined) { playPromise.then((_) => {}).catch((error) => {}); }
                console.log(" [ 2 - 1 ] First Audio / " + audio_param.pointer, audio_param.audiolists.length + "   audiolists : " + audio_param.audiolists[audio_param.pointer], audio_alarm.src);
            }
            else {
                console.log("[ 2 ] No Audio / audiolists.length = ", audio_param.audiolists.length);
            }
        }

        function alarmAudioList_Handler() {
            console.log("[ 1 ] / isPlaying : " + app_Info.isPlaying + " / isMute : " + app_Info.isMute);
            if (app_Info.isPlaying == false) {
                console.log(" [ 1 - 2 ] Push ok ! Check The Alarm Status ! ");
                if (((status_loc_1.exec_mon1 | status_loc_1.exec_mon2) > 0) && audio_param.is10LOCPushOK == false) //if((status_dme_1.exec_mon1 > 0) && audio_param.is10LOCPushOK == false)
                {
                    audio_param.audiolists.push(require("@/assets/sounds/sound_Pohang10_loc.mp3"));
                    console.log("  [ 1 - 3 ] LOC Push ok!");
                    audio_param.is10LOCPushOK = true;
                }
                if (((status_dme_1.exec_mon1 | status_dme_1.exec_mon2) > 0) && audio_param.is10DMEPushOK == false) //if((status_dme_1.exec_mon2 > 0) && audio_param.is10DMEPushOK == false)
                {
                    audio_param.audiolists.push(require("@/assets/sounds/sound_Pohang10_dme.mp3"));
                    console.log("  [ 1 - 3 ] DME Push ok!");
                    audio_param.is10DMEPushOK = true;
                }
                audio_param.audiolists_len = audio_param.audiolists.length;
                audio_param.audiolists_pop_len = audio_param.audiolists_len;
                console.log("  [ 1 - 4 ] Total audiolists.length : ", audio_param.audiolists_pop_len);
            }
        }

        audio_alarm.onended = function() {
            clearInterval(pushTimer); console.log(" [ 2 - 2 ] Stop Audio Push Timer!");

            if(audio_param.audiolists_len-- != 1) {
                audio_param.pointer++;
                audio_alarm.src = getAlarmSound(audio_param.pointer);
                console.log(" [ 2 - 3 ]  Next Audio / "+ audio_param.pointer, audio_param.audiolists.length + "   audiolists : " + audio_param.audiolists[audio_param.pointer], audio_alarm.src);
                var playPromise = audio_alarm.play();
                if (playPromise !== undefined) { playPromise.then((_) => {}).catch((error) => {}); }     
            }
            else {
                for(var index=0; index<audio_param.audiolists_pop_len; index++) {
                    audio_param.audiolists.pop(audio_param.audiolists[index]);
                }
                audio_param.pointer = 0;
                console.log(" [ 2 - 4 ] Audio All Pop ! / length zero Check : ", audio_param.audiolists.length);
                audio_last.muted = app_Info.isMute; console.log("[ 2 - next ] 음소거 체크 !! app_Info.isMute : ", app_Info.isMute);
                audio_last.play();
                console.log("  [ 2 - 5 ] Play The Audio 점검하세요!");
            }
        }

        const audio_last = new Audio(); 
        audio_last.src = require("@/assets/sounds/sound_last.mp3");

        audio_last.onended = function() {
            console.log("[ 3 ] Finish! Start Audio Push Timer! Push Restart!");
            app_Info.isPlaying = false;
            audio_param.is10LOCPushOK  = false;
            audio_param.is10DMEPushOK  = false;
            if(app_Info.isConnect)
            {
                pushTimer = setInterval(alarmAudioList_Handler, pushTimer_Period); console.log(" [ 3-1 ] Push Audio Timer Start !! ");
                playTimer = setInterval(audioAlarmPlay_Handler, playTimer_Period);  console.log(" [ 3-2 ] Play Timer Start !! ");
            }
        }


        return { status_all, status_loc_1, status_dme_1,
            airport_info, runway, app_Info, audio_param, statusFunctions, } // audioFunctions
    },
}

</script>

  
  
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
