    /* all content : each Equipment Status, Runway */
    height: 65%;
    box-sizing: border-box;
    display: flex;
    border-left-style: groove;
    border-right-style: groove;
}
div.box3-1 {
    flex: 1;
    display: flex;
    flex-direction: column;
}
div.box3-1-1 {
    flex: 1; 
}
div.box3-1-2 {
    /* display left / LOC */
    flex: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}
div.box3-1-3 {
    flex: 2;
}
div.box3-2 {
    flex: 5;
    display: flex;
    flex-direction: column;
}
div.box3-2-1 {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    text-align: center;
}
div.box3-2-2 {
    /* runway imge */
    flex: 2;
    background-image: url("@/../public/runway/runway_pohang_10.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50%;
    display: flex;
    /*justify-content : center;*/               /* (비행기 이미지를 box3-2-2 가운데에 위치 하기 위해 사용)  실제 사용할 때는 삭제   */
    align-items: center;
}
div.box3-2-3 {
    /* all GP DME */
    flex: 2;
    display: flex;
    flex-direction: row;
}
div.box3-2-3-1 {
    /* display left / GP DME*/
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
div.box3-2-3-2 {
    /* display right / GP DME*/
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}


div.box3-3 {
    flex: 1;
    display: flex;
    flex-direction: column;
}
div.box3-3-1 {
    flex: 1; 
}
div.box3-3-2 {
    /* display right / LOC */
    flex: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}
div.box3-3-3 {
    flex: 2;
}

div.box4 {
    /* footer */
    height: 15%;
