<template>

<div>
  <div v-if="tab.current_tab == 'menu'" class="tab-menu">
    <div @click="play(require('@/assets/sounds/sound_AirPlane.mp3'))"><button v-on:click="menu_btn_function('menu')">MENU</button></div>
    <div @click="play(require('@/assets/sounds/sound_AirPlane.mp3'))"><button v-on:click="menu_btn_function('main')">M A I N</button></div>
    <div @click="play(require('@/assets/sounds/sound_AirPlane.mp3'))"><button v-on:click="menu_btn_function('tab2')">INTERLOCK</button></div>
    <div @click="play(require('@/assets/sounds/sound_AirPlane.mp3'))"><button @click="menu_btn_function('tab3')">SYSTEM SETUP</button></div>
  </div>
  <div v-if="tab.current_tab == 'main'" class="tab-main">
    <CTU_Client_Pohang/>
  </div>
  <div v-else-if="tab.current_tab == 'tab2'" class="tab-interlock">
    <Move_Img/>
  </div>
  <div v-else-if="tab.current_tab == 'tab3'" class="tab-setup">
    <CTU_Client_Pohang/>
  </div>
</div>  

</template>

<script>
import { ref, reactive } from 'vue'
import CTU_Client_Pohang from '@/components/CTU_Client_Pohang.vue' 
import Move_Img from '@/components/Move_Img.vue'

export default {
  name: 'Menu', 
  components: {
    CTU_Client_Pohang,
    Move_Img,
  },
  setup() {
    const tab = reactive({ 
      current_tab: 'menu',
    });

    const menu_btn_function = (menu) => { // btn method
      tab.current_tab = menu;
    }

    function play(sound) { // https://mjmjmj98.tistory.com/31
            if (sound) {
                var audio = new Audio(sound);
                audio.play();
            }
        }

    return {tab, menu_btn_function, play}
  }
}

</script>



