<template>
        <div class="container">
            <h6 id="current-time"></h6>
        </div>
</template>

<script>
import { onMounted, getCurrentInstance } from "@vue/runtime-core";
export default {
    name: 'Time',
    setup() {
        const app = getCurrentInstance();
        const $socket = app.appContext.config.globalProperties.$socket;
