            setInterval(() => {
                const date = new Date(); // 새로운 Date 객체 생성
                time.innerHTML = date.toLocaleTimeString(); // toLocaleTimeString() Date 객체의 날짜의 시간 부분을 지역에 맞는 언어 포맷으로 반환하는 메소드
            }, 1000);
