                if(count_server + 1 < count_client)
                    connect = false;
                else 
                    connect = true;

                    console.log("node backend Server Connected : " + connect, count_client, count_server);
                    if(count_client == 10) count_client = 0; // 잘못됨.
                $socket.on('ping', (data) => {
                count_server = data;
            });
            }, 2000);*/
        });

        return { }
    },
}
</script>

