<template>
    <!--
    <input type="range" id="ex-in" min="0" max="100" step="2.5" value="30">
    <div id="ex-out"></div>
    -->
    <div class="slidecontainer">
        <img src="@/../public/etc/sound_on.png" width="20" height="20">
        <input type="range" min="0" max="100" step="5" class="slider" id="myRange" value="70">
        <p>Value: <span id="value"></span></p>
    </div>
</template>

<script>
import { onMounted, onUnmounted, reactive, computed, ref } from "@vue/runtime-core";
export default {
    name: 'Sound',
    setup() {
        const status = reactive({ 
            //volume: '70',
        });

        onMounted(() => {
            var slider = document.getElementById("myRange");
            var output = document.getElementById("value");
            output.innerHTML = slider.value;//slider.value;

            slider.oninput = function () {
                output.innerHTML = this.value;//this.value;
            }
        });

        return {}//status}
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.slidecontainer {
    width: 100%;
    margin-top: 0px;
}

.slider {
    appearance: none; 
    width: 25%;/* 슬라이더 길이 */
    height: 10px;/* 슬라이더 두께 */
    border-radius: 10px;/* 슬라이더 모서리를 약간 둥글게 */
    background: #b6b4b4;/* 슬라이더 색상 */
    outline: none;/* 슬라이더 테두리 없이 */
}

/* 슬라이더 핸들(클릭해서 드래그하는 부분) 커스텀 
        (Chrome, Opera, Safari, Edge 브라우저를 위해서는 -webkit-을,
        Firefox 브라우저를 위해서는 -moz- 를 사용하세요 */
.slider::-webkit-slider-thumb {
    -webkit-appearance: none;/* 기본 CSS 스타일을 적용하지 않기 위해서 */
    appearance: none;/* 기본 CSS 스타일을 적용하지 않기 위해서 */
    width: 15px;/* 슬라이더 핸들 길이 */
    height: 15px;/* 슬라이더 핸들 높이 */
    border-radius: 50%;/* 핸들 모양을 원모양으로 만들기 위함 */
    background: rgb(3, 28, 255);/* 슬라이더 핸들 색상 */
    cursor: pointer;/* 슬라이더 핸들에 마우스를 갖다대면 포인터로 변경 */
}
</style>