<script>
import { getCurrentInstance, onMounted, onUnmounted, reactive, computed, ref } from "@vue/runtime-core";
// @ is an alias to /src
import useSound from 'vue-use-sound'
import sound_AirPlane from '../assets/sounds/sound_AirPlane.mp3'
import sound_Alarm from '../assets/sounds/Alarm.wav'
import current_time from '@/components/Time.vue' // 사용
import Menu from '@/components/Menu.vue' 
import io from 'socket.io-client';
import statusFunctions from './status_bind.js'
export default {
    name: 'Pohang',
    props: { // 부모로 부터 데이터를 받을 수 있음. 허용할 props선언
    },
    components: {
        Menu,
        current_time, // 사용
    },
    setup() {
        //main.js에서 $socket에 저장했던 socket변수는 getCurrentInstance().appContext.config.globalProperties. $socket에 저장되어있다.
        const app = getCurrentInstance();
        const $socket = app.appContext.config.globalProperties.$socket;
        //const $socket = io('http://172.16.1.141:3000', {  autoConnect: false});

        const airport_info = {
            name : "Jeju Airport",
        };
        const runway = {
            left_num : "07",
            right_num : "25",     
        };
        const userInfo = reactive({
            userInfo: '',
        });

        const EQUIP_NUM1_IP = '172.16.1.54' // single_dvor
        const EQUIP_NUM2_IP = '172.16.1.58' // sfol_h_dme
        const EQUIP_NUM3_IP = '172.16.1.53' // dual_dvor

        const status = [{
            equip : "",
            maintenance : false,
            acvie : "tx1",
            tx1_onoff : false,
            tx2_onoff : false,
            exec_bypass : false,
            exec_mon1 : 3,
            exec_mon2 : 3,
            nfm_bypass : false,
            nfm_mon1 : 3,
            nfm_mon2 : 3,
            stby_bypass : false,
            stby_mon : 3,
            status : 3,
        }]
        const status_all = reactive({status});
        const status_loc_1 = reactive({status});
        const status_loc_2 = reactive({status});
        const status_gp_1 = reactive({status});
        const status_gp_2 = reactive({status});
        const status_dme_1 = reactive({status});
        const status_dme_2 = reactive({status});

        onMounted(() => {
            console.log(`컴포넌트가 마운트 됐습니다.`)
            //localStorage.setItem // 브라우저에 값을 저장해주는 저장소 // 추후 찾아보기

            $socket.on('SEND', (data) => {
                let server_msg;
                server_msg = JSON.parse(data); // JSON파싱
                console.log(`서버 소켓에게 받은 데이터: `, server_msg);
                
                if (server_msg.ip == EQUIP_NUM1_IP) {
                    status_loc_1.equip = server_msg.conf.equip;
                    status_loc_1.maintenance = server_msg.maint;
                    status_loc_1.acvie = server_msg.sys.active;
                    status_loc_1.tx1_onoff = server_msg.tx1.on;
                    status_loc_1.tx2_onoff = server_msg.tx2.on;
                    status_loc_1.exec_bypass = server_msg.exec.bypass;
                    status_loc_1.exec_mon1 = server_msg.exec.mon1;
                    status_loc_1.exec_mon2 = server_msg.exec.mon2;
                    status_loc_1.nfm_bypass = server_msg.nfm.bypass;
                    status_loc_1.nfm_mon1 = server_msg.nfm.mon1;
                    status_loc_1.nfm_mon2 = server_msg.nfm.mon2;
                    status_loc_1.stby_bypass = server_msg.stb.bypass;
                    status_loc_1.stby_mon = server_msg.stb.mon;
                }
                if (server_msg.ip == EQUIP_NUM2_IP) {
                    status_dme_1.equip = server_msg.conf.equip;
                    status_dme_1.maintenance = server_msg.maint;
                    status_dme_1.acvie = server_msg.sys.active;
                    status_dme_1.tx1_onoff = server_msg.tx1.on;
                    status_dme_1.tx2_onoff = server_msg.tx2.on;
                    status_dme_1.exec_bypass = server_msg.exec.bypass;
                    status_dme_1.exec_mon1 = server_msg.exec.mon1;
                    status_dme_1.exec_mon2 = server_msg.exec.mon2;
                    status_dme_1.nfm_bypass = server_msg.nfm.bypass;
                    status_dme_1.nfm_mon1 = server_msg.nfm.mon1;
                    status_dme_1.nfm_mon2 = server_msg.nfm.mon2;
                    status_dme_1.stby_bypass = server_msg.stb.bypass;
                    status_dme_1.stby_mon = server_msg.stb.mon;
                    //status_dme_1.status = server_msg.exec.mon1 | server_msg.exec.mon2 | server_msg.nfm.mon1 | server_msg.nfm.mon2 // loc/dme 따로
                }
                if (server_msg.ip == EQUIP_NUM3_IP) {
                    status_gp_1.equip = server_msg.conf.equip;
                    status_gp_1.maintenance = server_msg.maint;
                    status_gp_1.acvie = server_msg.sys.active;
                    status_gp_1.tx1_onoff = server_msg.tx1.on;
                    status_gp_1.tx2_onoff = server_msg.tx2.on;
                    status_gp_1.exec_bypass = server_msg.exec.bypass;
                    status_gp_1.exec_mon1 = server_msg.exec.mon1;
                    status_gp_1.exec_mon2 = server_msg.exec.mon2;
                    status_gp_1.nfm_bypass = server_msg.nfm.bypass;
                    status_gp_1.nfm_mon1 = server_msg.nfm.mon1;
                    status_gp_1.nfm_mon2 = server_msg.nfm.mon2;
                    status_gp_1.stby_bypass = server_msg.stb.bypass;
                    status_gp_1.stby_mon = server_msg.stb.mon;
                }
            });
        });

        function audio_play(sound, status) { // https://mjmjmj98.tistory.com/31
            var audio = new Audio(sound);
            if (status == true) {
                audio.play();
                audio.loop = true;
            }
            else { // 먹지를 않네 
                audio.pause();
                audio.loop = false;
                audio.muted = true;
            }
        }

        const [play, stop] = useSound(sound_Alarm, { interrupt: true });
        const [app_start] = useSound(sound_AirPlane, { interrupt: true });
        function alarm_sound(isMaint, total_status) {
            if (isMaint == false) {
                if (total_status == 1) {
                    //audio_play(require('@/assets/sound_AirPlane.mp3'), true);
                    play();
                }
                else {
                    //audio_play(require('@/assets/sound_AirPlane.mp3'), false);
                    stop;
                }
            }
        }

        return { status_all, status_loc_1, status_loc_2, status_gp_1, status_gp_2, status_dme_1, status_dme_2,
            airport_info, runway, statusFunctions, audio_play, app_start, alarm_sound }
    },
}
</script>
  
  
  
