const statusFunctions = {

    equipment_type, imagePath_exec, imagePath_stby, color_main, main_Status, mon_Status, selected_Status, selected_equip_status

}

function equipment_type(equip_type) {
    let equip_string;
    switch (equip_type) {
        case 4: equip_string = 'dvor';
            break;
        case 2: equip_string = 'loc';
            break;
        case 6: equip_string = 'gp';
            break;
        case 1: equip_string = 'dme';
            break;
        /*case 0 : equip_string = 'ffm';
            break;
        case 3 : equip_string = 'mkr';
            break;  
        case 5 : equip_string = 'cvor';
            break;*/
        default: equip_string = 'loc';
            break;
    }
    return equip_string;
};

function imagePath_exec(equip_type, isMaint, active_tx, tx1_onoff, tx2_onoff, mon1_status, mon2_status, isDual){
    let imgUrl;
    let status;
    let txOn;
    if(isDual == true)
        status = mon_Status(mon1_status, mon2_status);
    else
        status = mon1_status;            
    if(active_tx == "tx1")
        txOn = tx1_onoff;
    else
        txOn = tx2_onoff; 
    if (isMaint == false) {
        if (txOn == true) {
            // normal, warning, alarm, missing
            switch (status) {
                case 2:                                                                // (equip_type)
                    imgUrl = require('@/../public/equipment_status/equip_' + equipment_type(2) + '_alarm.png');
                    break;
                case 1:
                    imgUrl = require('@/../public/equipment_status/equip_' + equipment_type(2) + '_warning.png');
                    break;
                case 0:
                    imgUrl = require('@/../public/equipment_status/equip_' + equipment_type(2) + '_normal.png');
                    break;   
                default:
                    imgUrl = require('@/../public/equipment_status/equip_missing.png');
                    break;
                }
                return imgUrl;
        }
        else { // tx off
            imgUrl = require('@/../public/equipment_status/equip_' + equipment_type(2) + '_gray.png');
            return imgUrl;
        }
    } else  {
        // maintenance mode
        imgUrl = require('@/../public/equipment_status/equip_' + equipment_type(2) + '_blue.png');
        return imgUrl;
    }       
};

function imagePath_stby(isMaint, active_tx, tx1_onoff, tx2_onoff, mon1_status, mon2_status, isILS, isDual) {
    let imgUrl;
    let status;
    let txOn;
    if (isMaint == false) {
        if (isDual == true) {
            if (isILS == true)
                status = mon1_status;
            else
                status = mon_Status(mon1_status, mon2_status);
            if (active_tx == "tx1")
                txOn = tx2_onoff;
            else
                txOn = tx1_onoff;
            if (txOn == true) {
                // normal, warning, alarm, missing
                switch (status) {
                    case 2:
                        imgUrl = require('@/../public/equipment_status/equip_stby_alarm.png');
                        break;
                    case 1:
                        imgUrl = require('@/../public/equipment_status/equip_stby_warning.png');
                        break;
                    case 0:
                        imgUrl = require('@/../public/equipment_status/equip_stby_normal.png');
                        break;
                    default:
                        imgUrl = require('@/../public/equipment_status/equip_missing.png');
                        break;
                }
                return imgUrl;
            }
            else { // tx off
                imgUrl = require('@/../public/equipment_status/equip_stby_gray.png');
                return imgUrl;
            }
        }
        else { // single equip(Standby Path not used)
            imgUrl = require('@/../public/equipment_status/equip_stby_gray.png');
            return imgUrl;
        }
    } else { // maintenance mode
        imgUrl = require('@/../public/equipment_status/equip_stby_blue.png'); 
        return imgUrl;
    }       
};

function color_main(isMaint, main_status1) { // 추후 조건 추가해야함. 인터락 활주로 구분 // isInterlock, main_status2 등등
    let main_status = main_status1;
    let back_color;
    if (isMaint == false) { // no maintenance mode // 장비 전체에 대해서 추가해야 함
        if(main_status == 2) { // 장비 전체에 대해서 추가해야 함
            back_color = "#ff0000"; // alarm
        }
        else if(main_status == 1) {
            back_color = "#ffd000"; // warning
        }
        else if(main_status == 0) {
            back_color = "#16b330";  // normal
        }
        else { 
            back_color = "#000000"; // missing
        }
    } else { // maintenance
        back_color = "#1c19db";
    }
    return back_color;
}

function main_Status(isMaint, main_status1) { // isInterlock, main_status2 // 추후 수정
    //if(isInterlock == )
    let main_status = main_status1;
    if(isMaint == false) { // 장비 전체에 대해서 추가해야 함
        if(main_status == 0){ // 장비 전체에 대해서 추가해야 함
            return "N O R M A L";
        } else if (main_status == 1){
            return "W A R N I N G";
        } else if (main_status == 2){
            return "A L A R M";
        } else {
            return "M I S S I N G";                 
        }
    } else {
        return "MAINTENANCE";
    }
}

function mon_Status(mon1_status, mon2_status)
{
    let status;
    if(mon1_status == 0 && mon2_status == 0)
        status = 0; // Normal     
    else if(mon1_status == 3 && mon2_status == 3)
    {
        status = 3; // missing
    }            
    else  
    {
        if(mon1_status == 2 && mon2_status == 2)
            status = 2; // Alarm
        else 
            status = 1; // Warning
    }   
    return status;
}

function selected_Status(num1_MonStatus, num2_MonStatus) // pohang
{
}

function selected_equip_status(active_tx, tx1_onoff, tx2_onoff, mon1_status, mon2_status, isDual){ // pohang
    let status;
    let txOn;
    if(isDual == true)
        status = mon_Status(mon1_status, mon2_status);
    else
        status = mon1_status;            
    if(active_tx == "tx1")
        txOn = tx1_onoff;
    else
        txOn = tx2_onoff; 

    if (txOn == true) {
        return status;
    }
    else { // tx off
        return 2; // Alarm
    }
    
};

export default statusFunctions