<script>
import { getCurrentInstance, onMounted, onUnmounted, reactive, computed, ref } from "@vue/runtime-core";
import current_time from '@/components/Time.vue' // 사용
import Sound_set from '@/components/Sound.vue' // test
import ChildComp_Drag from '@/components/ChildComp_Drag.vue' // test
export default {
  name: 'Hello',
  props: { // 부모로 부터 데이터를 받을 수 있음. 허용할 props선언
    msg: String,
  },
  components :{
    current_time, // 사용
    Sound_set, // test
    ChildComp_Drag, // test
  },
  /* 어 떻 게 사 용? */
  setup() {
    let server_msg;

    //main.js에서 $socket에 저장했던 socket변수는 getCurrentInstance().appContext.config.globalProperties. $socket에 저장되어있다.
    const app = getCurrentInstance();
    const $socket = app.appContext.config.globalProperties.$socket;

    const status = reactive({ 
      userInfo: '',
      width:'',
      height:'',
    });

    const tab = reactive({ 
      current_tab: 'main',
      selected_btn: "main",
    });

    onMounted(() => {
      console.log(`컴포넌트가 마운트 됐습니다.`)
      status.userInfo = navigator.userAgent;
      status.width = window.innerWidth || document.body.clientWidth,
      status.height = window.innerHeight || document.body.clientHeight 
      console.log(status.userInfo); /*접속된 브라우저 및 기기 정보*/
      console.log(status.width, status.height);
      //localStorage.setItem // 브라우저에 값을 저장해주는 저장소 // 추후 찾아보기

      $socket.on('SEND', (data) => {
        server_msg = JSON.parse(data); // JSON파싱
        console.log(`서버 소켓에게 받은 데이터: `, server_msg);
      });
    });

    const menu_btn_function = (menu) => { // btn method
      tab.current_tab = menu;
      tab.selected_btn = menu;
    }
    
    return {status, tab, menu_btn_function, }
  }
}
</script>



